async function processDataAndDrawGraph(fileName) {
  const dataArray = [];
  const dateArray = [];

  let startDate = new Date(2024, 3, 6); // April 6, 2024 (year, month(0-indexed), day)

  try {
    const response = await fetch(fileName);
    const reader = response.body.getReader();

    let line;
    do {
      const result = await reader.read();
      line = result.value ? new TextDecoder().decode(result.value) : null;
      if (line) {
        const number = parseFloat(line); // Convert line to number
        dataArray.push(number);
        dateArray.push(new Date(startDate));
        startDate.setDate(startDate.getDate() + 1); // Increment date for next line
      }
    } while (line !== null);

    reader.releaseLock();

    // Draw the line graph using Chart.js (assuming you have Chart.js included)
    const ctx = document.getElementById('myChart').getContext('2d');
    const myChart = new Chart(ctx, {
      type: 'line',
      data: {
        labels: dateArray.map(date => date.toLocaleDateString()), // Format dates for labels
        datasets: [{
          label: 'Values',
          data: dataArray,
          backgroundColor: 'rgba(54, 162, 235, 0.2)',
          borderColor: 'rgba(54, 162, 235, 1)',
          borderWidth: 1
        }]
      },
      options: {
        title: {
          display: true,
          text: 'Line Graph'
        },
        scales: {
          xAxes: [{
            ticks: {
              autoSkip: false // Ensure all dates are displayed on x-axis
            }
          }]
        }
      }
    });

  } catch (error) {
    console.error('Error reading file:', error);
  }
}
path = './data.txt'
// Example usage: Assuming the file is named "data.txt" and the canvas element has id "myChart"
processDataAndDrawGraph(path);
